<!-- 群列表 -->
<template>
  <div class="group">
    <div v-for="(groupInfo,index) in groupsList" :key="index" class="group-item"> 
      <d-avatar class="avatar" size="30" :src="groupInfo.avatar"></d-avatar>
      <span class="group-name" :title="groupInfo.groupName">{{groupInfo.groupName}}</span>
      <d-checkbox size="medium" @input='selectGroup(groupInfo)' :value='isGroupSelected(groupInfo.groupId)' :disabled="isDisabled(groupInfo.groupId)"></d-checkbox>
      <d-icon icon="d-icon-more" size="mini" class="more" @click="openGroupList(groupInfo.groupId)"></d-icon>
    </div>
    <group-members-list 
    v-if="showGroupMembersList" 
    :groupId='clickGroupId'
    :returnMemberListValue='returnPageValue'
    @selectedMemberList='selectedMemberList'
    @closeAllCard='closeAllCard'
    @closePage='showGroupMembersList = false'
    @toGroupMemberPage='obtainGroupID'
    ></group-members-list>
  </div>
</template>

<script>
import _ from 'lodash';
import { mapGetters } from 'vuex';
import GroupMembersList from './GroupMembersList.vue';
import FileListMixin from '../File/FileList';
import { converseFullPinYin } from 'utils/pinyin.js';
export default {
  name: 'group-list',
  mixins: [ FileListMixin ],
  components:{
    GroupMembersList
  },
  props: [
    'searchInfo','returnPageValue'
  ],
  data() {
    return {
      showGroupMembersList: false,
      clickGroupId: '',
      initLength: 10,
      selectedGroups:{},
      groupID:'',
      oneGroupId:'',
      isTrue:'',// 拿到群成员传来的群id 与群列表id比对 有就true
      twoGroupId:''
    }
  },
  computed: {
    ...mapGetters([ 'group_list', 'members_range' ]),
    groupsList() {
      if (this.searchInfo.trim() === '') {
        return _.orderBy(
          _.filter(this.group_list, e => e.groupId),
          val => {
            return converseFullPinYin(val.pinyin.trim());
          },
          ['asc']
        ).slice(0, this.initLength)
      }
      return _.filter(this.group_list, e => e.groupName.indexOf(this.searchInfo) >= 0);
    }
  },
  mounted() {
    this.$Service.group.getGroupList(null, resp => {
      this.$store.commit('SET_GROUPLIST', resp.groups);
      this.$Service.log(`拉取群组列表：：：${JSON.stringify(resp.groups)}`);
    });
    this.$nextTick(() => {
      const cardBody = document.querySelector('.group');
      cardBody.addEventListener('scroll', (e) => {
        e.stopPropagation();
        this.loadMoreData(cardBody);
      })
    });
    if (this.returnPageValue.hasOwnProperty('group')) {// 如果vueX的值有group 标识 表示是group中传来的值
      this.selectedGroups = Object.assign({}, this.returnPageValue.group);// 把上面的值拷贝下来给selectedGroups
      this.$emit('selectMembers', {'group': this.selectedGroups});// 重新构造 有标识的对象 把对象抛出去给member页面使用
    }
  },
  methods: {
    openGroupList(id) {
      // let groupLength = (() => {
      //   for(var key in this.selectedGroups){
      //     return this.selectedGroups[key];
      //   }
      // })();
      if(Object.keys(this.selectedGroups).length >= 2){
         this.$toast.open({
            message: this.$t('file.forbid'),
            position: "is-top",
            type: "is-warning"
          });
        return;
      }
      // if(Object.keys(this.selectedGroups) != id){
      //   this.$toast.open({
      //     message: this.$t('file.forbid'),
      //     position: "is-top",
      //     type: "is-warning"
      //   });
      //   return;
      // }
      this.clickGroupId = id
      this.showGroupMembersList = true
    },
    // 滑动加载更多
    loadMoreData(el) {
      this.$nextTick(() => {
        if (this.isScrollToBottom(el)){
          // if (this.initLength < this.groupInfos.length) {//我的
          if (this.initLength < this.group_list.length) {
            if (this.group_list.length - this.initLength < 10) {
              this.initLength = this.group_list.length;
            } else {
              this.initLength += 10;
            }
          }
        }
      })
    },
    selectedMemberList(val) {
      console.log('group index页面收到所选的群成员',val.member)
      this.$emit('selectedMemberList',val)
    },
    closeAllCard() {
      this.showGroupMembersList = false;
      this.$emit('closeBothCard')
    },
    selectGroup(value) {
      console.log("inputMethods:", value);
      if (this.selectedGroups.hasOwnProperty(value.groupId)) {
        //如果当前选择的列表中已经有该成员，则删除它
        this.$delete(this.selectedGroups, value.groupId);
      } else {
        this.$set(this.selectedGroups, value.groupId, value);
      }
      console.log('this.selectedGroups',this.selectedGroups);
      let groupObj = {
        'group': this.selectedGroups
      }
      this.$emit('selectMembers', groupObj);
    },
    isGroupSelected(id) {
      // 处理群组的选中状态
      //if(this.members_range.hasOwnProperty('group')){
        if(this.returnPageValue.hasOwnProperty('group')){
        // 拿到ScopeFilter页面返回值 在页面上显示选中状态
        let groupKeys = Object.keys(this.returnPageValue.group);
        console.log('groupKeys', groupKeys);
        return groupKeys.includes(id);
      }
      // 处理群成员的群组选中状态
      if (this.returnPageValue.hasOwnProperty('member')) {
        let memberGroupId = [];
        _.forIn(this.returnPageValue.member, value => {
          memberGroupId.push(value.groupId);
        })
        return [...new Set(memberGroupId)].includes(id);
      }
    },
    isDisabled(id) {
      if (this.returnPageValue.hasOwnProperty('member')) {
      // if (this.members_range.hasOwnProperty('member')) {
        let memberGroupId = [];
        _.forIn(this.returnPageValue.member, value => {
        // _.forIn(this.members_range.member, value => {
          memberGroupId.push(value.groupId);
        })
        return [...new Set(memberGroupId)].includes(id);
      }
    },
    obtainGroupID(groupId) {
      this.oneGroupId = groupId
      if(this.oneGroupId !== undefined){
        let groupArr = []
        this.group_list.map(element => {
          groupArr.push(element.groupId)
        })
        this.isTrue = groupArr.includes(this.oneGroupId)
        // return groupArr.includes(this.oneGroupId);
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.group {
  width: 100%;
  height: 100%;
  overflow: hidden;
  &:hover {
    overflow: overlay;
  }
  .group-item {
    padding: 15px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    flex: 1;
    .avatar {
      margin-right: 10px;
    }
    .group-name {
      flex: 8;
      font-size: 14px;
      overflow: hidden;
      text-overflow: ellipsis;
      -o-text-overflow: ellipsis;
      white-space: nowrap;
      display: inline-block;
    }
  }
}
</style>